:root {
  --pri-font:"Poppins", sans-serif;
  --biggest-font-size: 2rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1rem;
  --normal-font-size: .938rem;
  --small-font-size: .813rem;
  --smaller-font-size: .75rem;
  --font-medium: 500;
  --font-semi-bold: 600;
  --font-black: 900;
}
@media screen and (min-width: 992px) {
  :root :root {
    --biggest-font-size: 4rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: .875rem;
    --smaller-font-size: .813rem;
  }
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --hue: 14;
  --first-color: hsl(var(--hue), 91%, 54%);
  --first-color-alt: hsl(var(--hue), 91%, 50%);
  --title-color: hsl(var(--hue), 4%, 100%);
  --text-color: hsl(var(--hue), 4%, 85%);
  --text-color-light: hsl(var(--hue), 4%, 55%);
  --body-color: linear-gradient(90deg, hsl(104, 28%, 40%) 0%, hsl(58, 28%, 40%) 100%) !important;
  --container-color: linear-gradient(136deg, hsl(104, 28%, 35%) 0%, hsl(58, 28%, 35%) 100%);
}

:root {
  --header-height: 3.5rem;
  --mb-0-25: .25rem;
  --mb-0-5: .5rem;
  --mb-0-75: .75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *::before, *::after {
  box-sizing: inherit;
  list-style: none;
}

body {
  margin: var(--header-height) 0 0 0;
  padding: 0;
  font-family: var(--pri-font);
  font-size: var(--normal-font-size);
  background: var(--body-color) !important;
  color: var(--text-color);
}

img {
  max-width: 100%;
  height: auto;
}

.section {
  padding: 4.5rem 0 2rem;
}
.section__title {
  font-size: var(--h2-font-size);
  margin-bottom: var(--mb-2);
  text-align: center;
}

.show-menu {
  top: 0 !important;
}

h1, h2, h3 {
  margin-top: 0;
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

ul {
  list-style: none;
}

a, a:visited, a:active {
  text-decoration: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

:root {
  --z-fixed: 100;
}

.header {
  width: 100%;
  background: var(--body-color);
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
}
.header .nav {
  height: var(--header-height);
}
.header .nav__logo-img {
  width: 1.25rem;
}
.header .nav__link, .header .nav__toggle, .header .nav__logo, .header .nav__closee {
  color: var(--title-color);
}
.header .nav__toggle {
  font-size: 1.25rem;
  cursor: pointer;
}
@media (max-width: 39.937em) {
  .header .nav__menu {
    position: fixed;
    width: 100%;
    background: var(--container-color);
    top: -150%;
    left: 0;
    padding: 3.5rem 0;
    transition: 0.4s;
    z-index: var(--z-fixed);
    border-radius: 0 0 1.5rem 1.5rem;
  }
  .header .nav__menu .button--ghost {
    border: 2px solid;
    background-color: transparent;
    border-radius: 3rem;
    padding: 0.75rem 1.5rem;
  }
  .header .nav__menu .button--ghost:hover {
    background: none;
  }
}
.header .nav__img {
  width: 6.25rem;
  position: absolute;
  top: 0;
  left: 0;
}
.header .nav__close {
  font-size: 1.8rem;
  position: absolute;
  top: 0.5rem;
  right: 0.7rem;
  cursor: pointer;
}
.header .nav__link {
  text-transform: uppercase;
  font-weight: var(--font-black);
  transition: 0.4s;
}
.header .nav__link:hover {
  color: var(--text-color);
}

.home__content {
  row-gap: 1rem;
}
.home__group {
  display: grid;
  position: relative;
  padding: 2rem;
}
.home__img {
  height: 15.625rem;
  justify-self: center;
}
.home__indicator {
  width: 0.5rem;
  height: 0.5rem;
  background-color: var(--title-color);
  position: absolute;
  border-radius: 50%;
  top: 7rem;
  right: 2rem;
}
.home__indicator::after {
  content: "";
  position: absolute;
  width: 1px;
  height: 3rem;
  background-color: var(--title-color);
  top: -3rem;
  right: 45%;
}
.home__details-img {
  position: absolute;
  right: 0.5rem;
  color: #fff;
}
.home__details-img h4 {
  font-weight: 700;
}
.home__details-title, .home__details-subtitle {
  display: block;
  font-size: var(--small-font-size);
  text-align: right;
}
.home__subtitle {
  font-size: var(--h3-font-size);
  text-transform: uppercase;
  margin-bottom: var(--mb-1);
}
.home__title {
  font-size: var(--biggest-font-size);
  font-weight: var(--font-black);
  line-height: 109%;
  margin-bottom: var(--mb-1);
}
.home__description {
  margin-bottom: var(--mb-1);
}
.home .button {
  display: inline-block;
  background-color: var(--first-color);
  color: var(--title-color);
  padding: 1rem 1.75rem;
  border-radius: 0.5rem;
  font-weight: var(--font-medium);
  transition: 0.3s;
}
.home .button:hover {
  background-color: var(--first-color-alt);
}
.home .button__icon {
  font-size: 1.25rem;
}
.home .button--link {
  color: var(--title-color);
}
.home .button-flex {
  display: inline-flex;
  align-items: center;
  column-gap: 0.5rem;
}
.home .swiper-pagination {
  display: block;
  margin-top: var(--mb-1);
}
.home .swiper-pagination-bullet {
  width: 0.3125rem;
  height: 0.3125rem;
  background-color: var(--title-color);
  opacity: 1;
}

